import { Stack } from '@mui/material';
import { CardTitleAndValue } from '@components/common/styles';
import { commaNotation, notateNumber } from '@utility/helpers';
const Grotto = ({
  hole
}) => {
  const {
    grotto
  } = hole?.caverns;
  const mushKills = commaNotation(Math.ceil(grotto?.mushroomKills));
  const mushKillsReq = commaNotation(Math.ceil(grotto?.mushroomKillsReq));
  return <>
    <Stack direction={'row'} gap={2} flexWrap={'wrap'} alignItems={'center'} data-sentry-element="Stack" data-sentry-source-file="Grotto.jsx">
      <CardTitleAndValue title={'Layer'} value={grotto?.layer} data-sentry-element="CardTitleAndValue" data-sentry-source-file="Grotto.jsx" />
      <CardTitleAndValue title={'Monarch HP'} value={notateNumber(grotto?.monarchHp, 'Big')} data-sentry-element="CardTitleAndValue" data-sentry-source-file="Grotto.jsx" />
      <CardTitleAndValue title={'Mushroom kills left'} value={`${mushKills} / ${mushKillsReq}`} data-sentry-element="CardTitleAndValue" data-sentry-source-file="Grotto.jsx" />
    </Stack>
  </>;
};
export default Grotto;